.card-overlay {
  color: white;
  border-radius: 8px;
  padding: 15px;
  pointer-events: none;
  display: flex;
  align-items: flex-end;
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  pointer-events: none;
  transition: opacity 300ms ease;
  opacity: 0;
  background: linear-gradient(
    180deg,
    transparent 62%,
    rgba(0, 0, 0, 0.00345888) 63.94%,
    rgba(0, 0, 0, 0.014204) 65.89%,
    rgba(0, 0, 0, 0.0326639) 67.83%,
