.tiles-container .tech-tags {
  display: flex;
  align-items: center;
  font-size: 1.2rem;
}

.tiles-container .other-info {
  font-size: 1.2rem;
}

.tiles-container .add-comment {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 90%;
}

.tiles-container .comments {
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 700px) {
  .tiles-container .add-comment {
    width: 100%;
  }
}

#post .post-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#post .post-header img {
  height: 60px;
  width: auto;
  border-radius: 50%;
  margin-left: 20px;
  margin-right: 30px;
}

#post .post-header .post-header-left h1 {
  font-size: 1.75rem;
  font-weight: bold;
}

#post .post-header-left {
  display: flex;
  align-items: center;
}

#post .post-header-right {
  display: flex;
  justify-content: flex-end;
}

#post .post-header-text {
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 700px) {
  #post .post-header .post-header-left h1 {
    font-size: 1.2rem;
  }
  #post .post-header .post-header-left h3 {
    font-size: 0.8rem;
  }
  #post .post-header-right {
    flex-direction: column;
    align-items: flex-end;
  }
  #post .post-header img {
    height: 40px;
    margin-right: 15px;
  }
  #post .post-header-right button {
    margin-right: 20px;
  }
}

.is-empty {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  padding: 2rem;
}

.is-empty svg {
  width: 50%;
  height: 100%;
}

.is-empty .subtitle {
  font-size: 1.5rem;
  margin-top: 1.5rem;
}

@media screen and (max-width: 700px) {
  .is-empty svg {
    width: 100%;
  }
  .is-empty .subtitle {
    text-align: center;
  }
}

#not-found {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 70vh;
}

#not-found-image {
  width: 100%;
  height: 100%;
}

#not-found h1 {
  font-size: 2rem;
  text-align: center;
}

#not-found p {
  font-size: 1.3rem;
  text-align: center;
}

:root {
  --color-primary: #ea4c89;
  --color-dark: #444444;
  --color-blue: #3a8bbb;
  --color-purple: #c142a0;
  --color-skyblue: #00b6e3;
  --color-green: #66cc99;
  --color-grey: #9da3a5;
}

.primary-text {
  color: #ea4c89;
  color: var(--color-primary);
}
.blue-text {
  color: #3a8bbb;
  color: var(--color-blue);
}
.green-text {
  color: #66cc99;
  color: var(--color-green);
}
.purple-text {
  color: #c142a0;
  color: var(--color-purple);
}
.dark-text {
  color: #444444;
  color: var(--color-dark);
}
.skyblue-text {
  color: #00b6e3;
  color: var(--color-skyblue);
}

.primary-bg {
  background: #ea4c89;
  background: var(--color-primary);
  color: white;
}
.dark-bg {
  background: #444444;
  background: var(--color-dark);
  color: white;
}
.purple-bg {
  background: #c142a0;
  background: var(--color-purple);
  color: white;
}

.primary-bg:hover {
  color: white;
}

.green-bg {
  background: #66cc99;
  background: var(--color-green);
  color: white;
}

.green-bg:hover {
  color: white;
}

html {
  overflow-y: auto;
}

body {
  font-family: Montserrat, sans-serif;
}

.full-height-spinner {
  min-height: 70vh;
  display: flex;
  align-items: center;
}

#root > .container:not(.navbar) {
  min-height: calc(100vh - 262px);
}
